.logoContainer {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	width: 100%;
	height: 225px;
}

.logoBack {
	z-index: -1;
	position:absolute;
	object-fit: cover;
	width: 100%;
	height: 100%;
}

.logoImage {
	height: fit-content;
	width: fit-content;
}